.left-col {
  background-size: cover;
  background-postion: center top;
  overflow: hidden;
  position: relative;
}
.left-col .slide {
  /*filter: grayscale(100%);*/
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: left top !important;
  background-size: cover !important;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1000ms cubic-bezier(1, 0.04, 0.355, 1) -webkit-transform, 1000ms cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-clip-path;
  transition: 1000ms cubic-bezier(1, 0.04, 0.355, 1) transform, 1000ms cubic-bezier(0.68, -0.55, 0.265, 1.55) clip-path;
  transition: 1000ms cubic-bezier(1, 0.04, 0.355, 1) transform, 1000ms cubic-bezier(0.68, -0.55, 0.265, 1.55) clip-path, 1000ms cubic-bezier(1, 0.04, 0.355, 1) -webkit-transform, 1000ms cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-clip-path;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  scale: 1;
  z-index: -1;
}
.left-col .slide.previous {
  z-index: 1;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
          clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  transition: 3s ease transform;
  will-change: transform;
}
.left-col .slide.previous.change {
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
}
.left-col .slide.next {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  z-index: 3;
  opacity: 1;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
          clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%);
}
.left-col .slide.current {
  opacity: 1;
  -webkit-transform: translateY(0) scale(1.25);
          transform: translateY(0) scale(1.25);
  z-index: 2;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

